import React from 'react'
import SchedulerContext from './SchedulerContext';

const SchedulerProvider = (props) => {
  const {
    children,
    color
  } = props; 

  const value = {
    color
  }

  return (
    <SchedulerContext.Provider value={value}>
      {children}
    </SchedulerContext.Provider>
  )
}
